import React, { Component } from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { performItemPost, deleteItem, fetchItems, likeItem } from '../actions/itemActions';
import { deleteList } from "../actions/listActions";
import { connect } from 'react-redux';
import { ITEMS_URL, USER_URL, LISTS_URL } from '../listrUrls';
import PropTypes from 'prop-types'


import { withStyles } from '@material-ui/core/styles';
import classnames from 'classnames';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';
import FavoriteIcon from '@material-ui/icons/Favorite';
import AddIcon from '@material-ui/icons/Add';
import Icon from '@material-ui/core/Icon';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import DeleteIcon from '@material-ui/icons/Delete';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ListSubheader from '@material-ui/core/ListSubheader';
import Grid from '@material-ui/core/Grid';
import withMobileDialog from '@material-ui/core/withMobileDialog';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Button from '@material-ui/core/Button';


const styles = theme => ({
  card: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
  },
  actions: {
    display: 'flex',
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
    marginLeft: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginRight: -8,
    },
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.danger,
  },
  warningBtn: {
    fontSize: theme.typography.pxToRem(15),
    color: "#ff1744",
  },
});

class ListDetail extends Component {

  constructor(props){
    super(props);
    this.state = {
      nestedModal: false,
      closeAll: false,
      itemName: null,
      itemDescription: null,
      loadedOnce: false,
      expanded: false,
      open: false,
      anchorEl: null,
    }
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.removeList = this.removeList.bind(this);
    this.toggleNested = this.toggleNested.bind(this);
    this.toggleAll = this.toggleAll.bind(this);
  }

  addItem(e){
    e.preventDefault();
    document.getElementById("newItemForm").reset();
    const itemData = {
      "name": this.state.itemName,
      "static_id": this.makeid(),
    }
    if(this.state.itemDescription){
      itemData.description = this.state.itemDescription;
    }
    this.props.performItemPost(ITEMS_URL, itemData, this.props.data.static_id, this.props.token);
    this.setState({
      itemName: null,
      itemDescription: null,
    });
  }
  deleteItem(e, itemID, listID){
    e.preventDefault();
    this.props.deleteItem(ITEMS_URL, itemID, listID, this.props.token);
  }
  likeItem(e, itemData, listID){
    e.preventDefault();
    fetch(USER_URL, {
      method: 'GET',
      headers: {
        'content-type': 'application/json',
        'Authorization': 'Token ' + this.props.token,
      },
    })
    .then(res => res.json())
    .then(data => {
      if(data.username){
        var index = itemData.liked_users.indexOf(data.username);
        if (index > -1) {
          console.log('REMOVED USER')
          itemData.liked_users = [];
        }else{
          itemData.liked_users.push(data.username);
        }
        this.props.likeItem(ITEMS_URL, itemData, listID, this.props.token);
      }
    });
  }
  removeList(e, listId){
    if(this.props.token){
      this.props.deleteList(LISTS_URL, listId, this.props.token);
      this.toggleAll();
    }
  }

  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < 8; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  toggleNested() {
    this.setState((prevState) => ({
      nestedModal: !prevState.nestedModal,
      closeAll: false,
    }));
  }

  toggleAll() {
    this.setState({
      nestedModal: !this.state.nestedModal,
      closeAll: true
    });
  }

  handleClickOpen = (e) => {
    this.props.fetchItems(ITEMS_URL, this.props.data.static_id, this.props.token);
    this.setState((prevState) => ({
      open: true,
    }));
  }

  handleClose = (e) => {
    this.setState((prevState) => ({
      open: false,
    }));
  }
  handleMenuOpen = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
  };

  expandPanel = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };

  render () {

    const { classes, fullScreen } = this.props;

    var data;
    var hideRemove;
    if(this.props.listType !== undefined && this.props.listType === 'shared'){
      hideRemove = {
        display: 'none',
      }
    }
    if(this.props.isOpen && this.props.loading){
      data = (
        <div class="card">
          <div class="card-header text-center" id="headingOne">
            <h5 class="mb-0">
              <a class="btn btn-link disabled" type="button">
                Loading
              </a>
            </h5>
          </div>
        </div>
      );
      this.props.fetchItems(ITEMS_URL, this.props.data.static_id, this.props.token);
    }
    if(this.props.items && this.props.items.length > 0){
      data = this.props.items.map(item => {
          var liked;
          if(item.liked_users.length > 0){
            liked = <span class="badge badge-danger badge-pill">{item.liked_users.length}</span>
          }
          if(item.description !== ""){
            const target = "#"+item.static_id;
            const showNewLine = {
              whiteSpace: "pre-wrap",
              flexGrow: 1
            }
            return (
              <ExpansionPanel expanded={this.state.expanded === item.static_id} onChange={this.expandPanel(item.static_id)}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography className={classes.heading}>{item.name}</Typography>
                  <Typography className={classes.secondaryHeading}>{item.owner}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                  <Typography style={showNewLine}>
                    {item.description}
                  </Typography>
                  <Button className={classes.warningBtn} onClick={(e) => this.deleteItem(e, item.static_id, this.props.data.static_id)}>Remove</Button>
                </ExpansionPanelDetails>
              </ExpansionPanel>
          );
        }else{
          return (
            <ExpansionPanel expanded={this.state.expanded === item.static_id} onChange={this.expandPanel(item.static_id)}>
              <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                <Typography className={classes.heading}>{item.name}</Typography>
                <Typography className={classes.secondaryHeading}>{item.owner}</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Button className={classes.warningBtn} onClick={(e) => this.deleteItem(e, item.static_id, this.props.data.static_id)}>Remove</Button>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          );
        }
      });
    }else{
      data = (
        <ListItem button>
          <ListItemText primary="This List Has No Items" />
        </ListItem>
      );
    }
    return(
      <Grid item key={this.props.data}  xs={12} sm={12} md={4} lg={3}>
        <Card className={classes.card}>
          <CardHeader
          action={
            <IconButton onClick={this.toggleNested}>
              <DeleteIcon />
            </IconButton>

          }
          title={this.props.data.name}
          subheader={this.props.data.owner}
          />
          <CardActions>
            <div>
              <Button onClick={this.handleClickOpen}>View</Button>
              <Dialog
                fullScreen={fullScreen}
                open={this.state.open}
                onClose={this.handleClose}
                aria-labelledby="form-dialog-title"
                >
                <DialogTitle id="form-dialog-title">{this.props.data.name}</DialogTitle>
                <DialogContent>
                  <form id="newItemForm">
                    <FormControl margin="normal" required fullWidth>
                      <InputLabel htmlFor="itemName">Item Name</InputLabel>
                      <Input id="itemName" name="text" required autoFocus onChange={(e) => this.handleChange(e)}/>
                    </FormControl>
                    <FormControl margin="normal" required fullWidth>
                      <InputLabel htmlFor="itemDescription"
                      required={false}>Item Description (Optional)</InputLabel>
                      <Input
                        name="itemDescription"
                        type="text"
                        multiline
                        id="itemDescription"
                        autoComplete='off'
                        onChange={(e) => this.handleChange(e)}
                      />
                    </FormControl>
                    <Button
                      fullWidth
                      variant="raised"
                      color="primary"
                      onClick={(e) => this.addItem(e)}
                      >
                      Add Item
                    </Button>
                  </form>
                  <br />
                  {data}
                </DialogContent>
                <DialogActions>
                  <Button onClick={this.handleClose} color="primary">
                    Cancel
                  </Button>
                </DialogActions>
              </Dialog>
            </div>
          </CardActions>
          <Dialog
            fullScreen={fullScreen}
            open={this.state.nestedModal}
            onClose={this.toggleAll}
            aria-labelledby="responsive-dialog-title">
            <DialogTitle id="responsive-dialog-title">{"Are Your Sure You Want to Delete this List?"}</DialogTitle>
            <DialogContent>
              <DialogContentText>
                All items will be removed, collaborators won't be able to view it, and you might get sad...
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={(e) => this.removeList(e, this.props.data.static_id)} autoFocus className={classes.warningBtn}
                fullWidth>
                Yes, Delete This List
              </Button>
            </DialogActions>
          </Dialog>
        </Card>
      </Grid>

    );
  }
}

ListDetail.proptypes = {
  performItemPost: PropTypes.func.isRequired,
  deleteItem: PropTypes.func.isRequired,
  deleteList: PropTypes.func.isRequired,
  likeItem: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  fullScreen: PropTypes.bool.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  loading: state.items.loading,
  items: state.items.items
});
var ToExport = withStyles(styles)(ListDetail);
ToExport = withMobileDialog()(ToExport);
export default connect(mapStateToProps, { performItemPost, deleteItem, fetchItems, likeItem, deleteList })(ToExport);



